import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Events } from '@ionic/angular';

import { FormBuilder, FormGroup } from '@angular/forms';

// @ts-ignore => yes its there otherwise it wouldn't compile
import { UserSandbox } from '@sandboxes/user.sandbox';

@Component({
  selector: 'app-register',
  templateUrl: './register.page.html',
  styleUrls: ['./register.page.scss'],
})
export class RegisterPage implements OnInit {
  subscription;
  registerForm: FormGroup;

  constructor(private router: Router,
              private formBuilder: FormBuilder,
              private userSandbox: UserSandbox,
              private events: Events,
  ) {
  }

  ngOnInit() {
    this.registerForm = this.formBuilder.group({
      firstName: [''],
      lastName: [''],
      email: [''],
      password: [''],
      repeatPassword: [''],
      mobilePhone: [''],
      companyName: ['']
    });
  }

  onRegister() {
    this.userSandbox.register(this.registerForm.value);
    this.subscription = this.events.subscribe('login:success', () => {
      if (this.subscription) {
        this.subscription.unsubscribe();
      }
      this.router.navigate(['tabs']);
    });
  }

  onSignIn() {
    this.router.navigate(['login']);

  }


}
